body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.watch {
  border: 3px dotted;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch__axis {
  position: absolute;
}

.watch__axis:after {
  content: "";
  background-color: #ff0;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  display: block;
}

.watch__second {
  transform-origin: bottom;
  background-color: green;
  animation: 5s linear infinite arrows;
  position: absolute;
  top: 0;
}

.watch__second:after {
  content: "";
  width: 2vmin;
  height: 40vmin;
  display: block;
}

.watch__minut {
  transform-origin: bottom;
  background-color: #00f;
  animation: 60s infinite arrows;
  position: absolute;
  top: 20vmin;
}

.watch__minut:after {
  content: "";
  width: 4vmin;
  height: 20vmin;
  display: block;
}

@keyframes arrows {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

